
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

// import jsonp from 'jsonp'

export default {
name: 'marklist-view',
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
XInput,
XButton,
Group
},
data () {
return {
allTask:[],
txtValue:''
}
},
computed: {
realDataArr (){
// console.log(this.allTask.slice(0,this.allTask.length));
return this.allTask.slice(0,this.allTask.length);
}
},
filters:{

},
methods: {
onButtonClickDel (index) {
this.allTask.splice(index-1,1);
for(var i = 0,l=this.allTask.length;i<l;i++){
this.allTask[i]['id'] = i+1;
}
if(window.localStorage){
window.localStorage.setItem('hbAllTask',JSON.stringify(this.$data.allTask));
}
// console.log(this.allTask);
},
handleEvents (type) {
console.log('event: ', type)
},
xtrim (str) {
return str.replace(/(^\s*)|(\s*$)/g, ""); 	 //去除首尾空格
},
onBtnClick () {
if(!this.xtrim(this.txtValue)){
alert('便签内容不能为空');
return;
}
var nowId = this.allTask.length + 1;
this.allTask.push({
title:this.xtrim(this.txtValue),
id:nowId
});
this.txtValue = '';
if(window.localStorage){
window.localStorage.setItem('hbAllTask',JSON.stringify(this.$data.allTask));
}
}
},
created: function () {
this.$nextTick(function(){
// 	jsonp('https://api.douban.com/v2/movie/top250?count=8', null, function (err, data) {
//   if (err) {
//     console.error(err.message);
//   } else {
//     console.log(data);
//   }
// });
if(window.localStorage && window.localStorage.getItem('hbAllTask')){
this.allTask = JSON.parse(window.localStorage.getItem('hbAllTask'));
return;
}
this.allTask = this.$store.getters['marklist/getAllT'];
})
}
}



