
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'

import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


// console.log(vm)

export default {
  name: 'app',
  data () {
    return {
      isSelected:0
    }
  },
  filters: {
    json2 :function(value) {
      return JSON.stringify(value).split(',');
    }
  },
  components: {
    Group,
    Cell,
    XHeader,
    ViewBox,
    Tabbar,
    TabbarItem
  },
  created () {
    this.$nextTick(() => {
      // console.log(222)
      this.initTabStatus();
      // console.log(vm)
      // console.log(TabbarItem.data().simple);
      // this.$emit('on-item-click', this.currentIndex)
    })
  },
  methods: {
    onItemClickCb :function(currentIndex){
        console.log(currentIndex)
        // console.log(this.currentIndex)
        if(currentIndex === 2){
            this.$router.push('/dashboard')
        }else if(currentIndex === 1){
            this.$router.push('/mov')
        }else{
            this.$router.push('/')
        }
    },
    initTabStatus:function(){
      if(this.$router.currentRoute.path === '/dashboard'){
        this.isSelected = 2;
      }else if(this.$router.currentRoute.path === '/mov'){
        this.isSelected = 1;
      }else{
        this.isSelected = 0;
      }
    }
  },
  watch:{

  },
  computed: {
    // a computed getter
    reversedMessage: function () {
      // `this` points to the vm instance
      if(this.picked == 'one'){
        this.isClass = false;
      }else{
        this.isClass = true;
      }
      return this.isClass;
    }
  }
}



